@import '/node_modules/@radix-ui/colors/black-alpha.css';
@import '/node_modules/@radix-ui/colors/violet.css';

.container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 15px;

  button {
    /* reset */
    all: unset;
  }

  .CheckboxRoot {
    background-color: white;
    width: 25px;
    height: 25px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 10px var(--black-a7);
    cursor: pointer;
  }

  .CheckboxRoot:hover {
    background-color: var(--violet-3);
  }

  .CheckboxRoot:focus {
    box-shadow: 0 0 0 2px black;
  }

  .CheckboxIndicator {
    color: var(--violet-11);
  }

  .Label {
    color: white;
    padding-left: 15px;
    font-size: 15px;
    line-height: 1;
  }
}
