.form {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.containerText {
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  margin: 30px 0 50px;
  gap: 10px;
}

.containerFields {
  display: flex;
  flex-flow: column nowrap;
  gap: 20px;
  width: 300px;
}
.fields {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap: 4px;
}

.captcha {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  gap: 12px;
  width: 100%;

  img {
    border-radius: 5px;
    box-shadow: 0 2px 10px var(--black-a7);
  }

  input {
    height: 40px;
    border-radius: 5px;
    border: none;
    outline: none;
    max-width: 200px;
    width: 100%;
    text-align: center;
    font-weight: 600;
    font-style: italic;
    font-size: 16px;
    color: #0bbef6;
  }
}

.submit {
  width: 100%;
  height: 40px;
  border: 1px solid transparent;
  border-radius: 5px;
  cursor: pointer;
}

.tooltipContainer {
  position: relative;
  display: inline-block;

  &::after {
    content: attr(data-tooltip);
    position: absolute;
    bottom: -35px;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    display: none;
    padding: 5px;
    border-radius: 4px;
    background-color: #6e6e6e;
    color: white;
    font-size: 12px;
    z-index: 10;
  }

  &:hover::after {
    display: block;
  }
}
